import { useState, useEffect } from 'react';
import { apiTweetAction, apiGetTweetData, apiTweetFeed } from '../tweet_functions'
import { UserLink, UserPicture, UserDisplay } from '../../profiles'
import TweetOptionsBtn from './TweetOptionsBtn'

import '../styles/Tweet.css'

 
function Tweet({ tweet, className, currentUser, nextUrl, callback, focusedTweetId, setFocusedTweetId, idx}) { 
    const [didLike, setDidLike] = useState(false);
    const [didRetweet, setDidRetweet] = useState(false);
    const [likes, setLikes] = useState();
    const [didLookupTweet, setDidLookupTweet] = useState(false);



    // EVENT FUNCTIONS 
    const handleLink = (e) => {
        e.preventDefault()
        e.stopPropagation()
        window.location.href = `/${tweet.id}`
    }  
    
    function handleTweetAction(action, event) {
        event.stopPropagation()
        event.preventDefault()
        

        let currentAction = action
        
        if (action === 'like') {
            if (didLike) {
                currentAction = 'unlike'
            }
        } else if (action === 'retweet' & didRetweet) {
            return
        }
        apiTweetAction(tweet.id, currentAction, handleBackendActionEvent)    
    }
    
    // CALLBACKS
    const handleBackendActionEvent = (response, status) => {
        if (status === 403) {
            window.href = "/login/"
        } else if (status !== 404) {
            if (response.details === 'retweeted') {
                apiTweetFeed(currentUser, callback, nextUrl)
            } else {
                apiGetTweetData(handleTweetDataCallback, tweet.id)
            }
        }
    }

    const handleTweetDataCallback = (response, status) => {
        if (status === 200) {
            setDidRetweet(response['didRetweet']);
            setDidLike(response['didLike']);
            setLikes(response['likes']);
            setDidLookupTweet(true);

            if (response['likes'] === 0) {
                setDidLike(false)
            }

        } else {
            console.log(response, status)
        }
    }

    // EFFECTS
    useEffect(() => {
        const fetchData = async () => {
            if (!isMounted) {return}
            if (!didLookupTweet && tweet) {
                apiGetTweetData(handleTweetDataCallback, tweet.id);
            }
        }
        let isMounted = true
        fetchData()
        return () => {
            isMounted = false;
        }
    }, [didLookupTweet, focusedTweetId, tweet]); 
    
    let willRender = (didLookupTweet && tweet && (tweet.content || tweet.parent))

    // CONTENT


    const Retweet = () => {
        const [loading, setLoading] = useState(true);
        return (loading) ? (
            <div onClick={((e) => handleLink(e))}>
                <div className="retweet-header__container">
                    <div className="retweet-header">retweeted by <UserLink user={tweet.user}>{tweet.user.username}</UserLink></div>
                    
                </div>
                <Tweet loading callback={callback} setLoading={setLoading} 
                tweet={tweet.parent} 
                className={tweet.content ? "retweet" : "retweet no-border"} 
                currentUser={currentUser} setFocusedTweetId={setFocusedTweetId} focusedTweetId={focusedTweetId} key={`${idx}-${tweet.id}`}/>
            </div>
        ) : null
    }

    return willRender ? (
        <div className={className + " overall-container"} onClick={((e) => handleLink(e))}>
            {!tweet.parent &&
            <div className="profile-container d-flex">
                <div className="mx-1 profile-picture">
                    {tweet.user ? <UserLink><UserPicture user={tweet.user}/></UserLink>: "Loading..."}
                </div>
                <div className="tweet-header">
                    <UserDisplay user={tweet.user} /> 
                </div>
                <TweetOptionsBtn tweet={tweet} currentUser={currentUser}/>
            </div>}
            <div className="tweet-container">
                {tweet.content !== "" &&
                <div className="tweet-content-wrapper">
                    <p className="tweet-text">{`${tweet.content}`}</p>
                </div>
                }
                
                {!tweet.parent ?
                    <div className="buttons">
                        <div className="rt-btn__container">
                            <i className={didRetweet ? `fas fa-retweet btn-icon rt-icon text-green` : "fas fa-retweet btn-icon rt-icon"} onClick={((e) => handleTweetAction("retweet", e))}></i>
                        </div>
                        <div className="like-btn__container">
                            <i className={didLike ? "fas fa-heart btn-icon like-icon red-text" : "far fa-heart btn-icon like-icon"} onClick={((e) => handleTweetAction("like", e))}></i>
                            {likes}
                        </div>
                    </div>
                : 
                <div className="retweet-container">
                    <Retweet/>
                </div>}
            </div>
        </div>
    ) : null;
}

export default Tweet;


