import React, {useState} from 'react';
import {ProfileBadgeComponent} from '../profiles'
import TweetListFrame from './tweets/TweetListFrame'
import {ProfileNav} from './profile'
import './styles.css'
import './feed/styles.css'
import {apiGetUserTweets, apiGetUserRetweets, apiGetLikedTweets} from '../api/tweets'


function ProfileComponent(props) {
    const [activeView, setActiveView] = useState(0);
    const {currUser, className, handleLoadNextResponse, nextUrl, callback, setCurrUser} = props;
    
    return (
        <div className="profile twitter_feed">
            <div className="profile__header">
                <p className="profile__content">{currUser ? currUser.username : ""}</p>
            </div>
            <div className="profile__body">
                <div className="profile">
                    <ProfileBadgeComponent currUser={currUser} className={className} handleLoadNextResponse={handleLoadNextResponse} setCurrUser={setCurrUser}/>
                </div>
                <ProfileNav activeView={activeView} setActiveView={setActiveView}/>
                {activeView === 0 && <TweetListFrame currUser={currUser} profileOwner={currUser} apiFunction={apiGetUserTweets} />}
                {activeView === 1 && <TweetListFrame currUser={currUser} profileOwner={currUser} apiFunction={apiGetUserRetweets} />}
                {activeView === 2 && <TweetListFrame currUser={currUser} profileOwner={currUser} apiFunction={apiGetLikedTweets}/>}
                
            </div>
        </div>
    );
}

export default ProfileComponent;
